import useOnclickOutside from 'react-cool-onclickoutside';
import React, { useState, useEffect } from 'react';
import { createUseStyles } from 'react-jss';
import classnames from 'classnames';

export default function TableCell({ children, className, data, ...props }) {
    const styles = createUseStyles({
        cell: {
            textOverflow: 'ellipsis',
            overflow: 'hidden',
            maxWidth: '20rem',
            padding: 10,
        },
        input: {
            backgroundColor: 'rgba(0, 0, 0, 0.5)',
            fontSize: 'inherit',
            width: '100%',
        },
    });
    const classes = styles();

    const [dataState, setDataState] = useState(data);
    const [editing, setEditing] = useState(false);

    const cell = useOnclickOutside(() => {
        setEditing(false);
        // save row
    });

    return (
        <td className={classnames(classes.cell, className)} onDoubleClick={() => setEditing(true)} ref={cell} {...props}>
            {
                editing
                    ? <input className={classnames(classes.input)} value={dataState} onChange={e => setDataState(e.target.value)} type="text" />
                    : children
            }
        </td>
    );
}
